export default {
	data() {
		return {
			userphone: '',
			usercode:'',
			userpwd:'',
			type:'',
			next:false
		}
	},
	onLoad() {

	},
	methods: {
		checkmobile(){
			var token,that=this
			if(!(/^1(3|4|5|6|7|8|9)\d{9}$/.test(that.userphone))){ 
				uni.showToast({
				    title: '请填写正确手机号',
					 icon: 'none',
				    duration: 2000
				});
				return false; 
			} 
			uni.getStorage({
				key: 'token',
				success: function (res) {
					token=res.data
					uni.request({
						url: getApp().globalData.APIurl+'/api/wxapp/index/checkMobile',
						data: {
						    mobile: that.userphone,
						},
						header: {
						    'Token': token ,
							'Device-Type':'wxapp'
						},
						success: (res) => {
							if(res.data.code){
								that.next=true
							}else{
								uni.showToast({
								    title: '该手机号已被绑定',
									 icon: 'none',
								    duration: 2000
								});
							}
						}
						
					})
				}
			});
		},
		tosubmit(){
			var token,that=this
			uni.getStorage({
				key: 'token',
				success: function (res) {
					token=res.data
					uni.request({
						url: getApp().globalData.APIurl+'/api/user/Profile/bindingMobile',
						data: {
						    mobile: that.userphone,
							verification_code:that.usercode,
							password:that.userpwd
						},
						header: {
						    'Token': token ,
							'Device-Type':'wxapp'
						},
						success: (res) => {
							console.log(res)
							if(res.data.code){
								uni.showToast({
								    title: '绑定成功',
									icon:'none',
								    duration: 2000,
									success:function(){
										setTimeout(()=>{
											uni.switchTab({
											    url: '/pages/mine/index'
											});
										},2000)
										
									}
								});
							}
						}
					})
				}
			});
		},
		focusBox(t){
			this.type=t
		},
		blurBox(){
			this.type=''
		},
		changeBox(){
			this.next=false
		}
	}
}
